.container{
  position: relative;
  display: flex;
  justify-content: center;
  align-items: center;
  padding: 40px 0;
  flex-wrap: wrap;
}

.box{
  position: relative;
  color: #fff;
  background: #111;
  display: flex;
  justify-content: center;
  
}


.box::before{
  content: '';
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background: linear-gradient(45deg, #df7aec, #00d0ff);
}

.box::after{
  content: '';
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background: linear-gradient(45deg, #224646,#088080);
  filter: blur(30px);
}


.box .content{
  position: relative;
  z-index: 10;
  padding: 20px 40px;
}



.box .content p{
  font-size: 1.1em;
  color: #fff;
  margin-bottom: 10px;
  line-height: 1.4em;
}





